.containerAboutPageTwo{
    min-width: 100vw;
    min-height: 100vh;
    max-width: 100vw;
    max-height: 100vh;
    margin-top: 10vh;
    margin-bottom: 10vh;
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
    .sectionOneAboutPageTwo{
        z-index: 3;
        min-width: 35vw;
        min-height: 100vh;
        .sectionContentOneAboutPageTwo{
            min-height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .one{
                position: absolute;
                min-width: 10vw;
                border: 1px solid  #707070;
            }
            .two{
                position: absolute;
                min-width: 5vw;
                border: 1px solid  #707070;
            }
            h2{
                z-index: 5;
                position: absolute;
                font-family: PpeikoLight;
                font-size: 6rem;
                font-weight: 400;
                color: #E0BE8D;
                min-width: 30vw;
                margin-top: 55vh;
                margin-right: 5vw;
                text-align: right;
            }
            h1{
                min-width: 30vw;
                z-index: 4;
                position: absolute;
                font-family: PpeikoMedium;
                font-size: 30rem;
                font-weight: 400;
                margin-right: 15vw;

            }
            .sello1{
                position: absolute;
                min-width: 8vw;
                min-height: 1.5rem;                
                background-color: #E0BE8D;
                margin-bottom: 10vh;
                margin-right: 154vw;
                z-index: 20;
            }
            .sello2{
                position: absolute;
                min-width: 8vw;
                margin-right: 35vw;
                margin-top: 100vh;
                min-height: 1.5rem; 
                background-color: #E0BE8D;
            }
        }
    }
    .sectionTwoAboutPageTwo{
        z-index: 2;
        min-width: 60vw;
        min-height: 100vh;
        background-image: url('../../img/about/pagetwo/fondo.png');
        background-size: cover;
        background-repeat: no-repeat;
    }
    .sectionThreeAboutPageTwo{
        position: absolute;
        z-index: 3;
        min-width: 100vw;
        min-height: 40vh;
    }
}