.containerGeneralFooter{
    min-width: 100vw;
    min-height: 30vh;
    background-image: url('../../img/footer/fondo.png');
    background-repeat: no-repeat;
    background-size: 100%;

    .sectionOneFooter{
        min-height: 20vh;   
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #C7974B;
    }
    .sectionTwoFooter{
        min-height: 10vh;   

        ul{
            min-height: 10vh;
            list-style: none;            
            min-width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            li{
                min-width: 10%;
                color: #E0BE8D;
                font-size: 1.2rem;
            }
        }
    }
}