#root{
margin: 0;
padding: 0;
}
body {
  border: 1px solid red;
  margin: 0;
  padding: 0;
  border: 1px solid blue;
  width: 100vw;
  max-width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



/* PPEIKO */
@font-face {
  font-family: PpeikoBlack;
  src: local('fonts'),
   url("./fonts/ppeiko/PPEiko-BlackItalic.otf") format("opentype");
}
@font-face {
  font-family: PpeikoHeavy;
  src: local('fonts'),
   url("./fonts/ppeiko/PPEiko-Heavy.otf") format("opentype");
}
@font-face {
  font-family: PpeikoLight;
  src: local('fonts'),
   url("./fonts/ppeiko/PPEiko-LightItalic.otf") format("opentype");
}
@font-face {
  font-family: PpeikoMedium;
  src: local('fonts'),
   url("./fonts/ppeiko/PPEiko-Medium.otf") format("opentype");
}
@font-face {
  font-family: PpeikoThin;
  src: local('fonts'),
   url("./fonts/ppeiko/PPEiko-Thin.otf") format("opentype");
}
/* DRUK */
@font-face {
  font-family: Druk;
  src: local('fonts'),
   url("./fonts/druk/Druk-Medium.ttf") format("opentype");
}




code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',PpeikoBlack,
  PpeikoHeavy,
  PpeikoLight,
  PpeikoMedium,
  PpeikoThin,
  Druk,
    monospace;
}
