.containerBeautyPageThree{
    min-width: 100vw;
    min-height: 100vh;
    max-width: 100vw;
    max-height: 100vh;
    display: flex;
    align-items: flex-end;

    .sectionOneBeautyPageThree{
        z-index: 3;
        min-width: 35vw;
        min-height: 100vh;
        .sectionContentOneBeautyPageThree{
            min-height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .one{
                display:none;    
            }
            .two{
                position: absolute;
                min-width: 25vw;
                max-width: 25vw;
                margin-bottom: -40vh;
                margin-left: 0vw;
            }
            h2{
                z-index: 5;
                position: absolute;
                font-family: PpeikoLight;
                font-size: 5rem;
                font-weight: 400;
                color: #E0BE8D;
                margin-left: -4vw;
                margin-bottom: 0vh;
                margin-top: 0;
         
            }
            h1{
                display: none;
                z-index: 4;
                position: absolute;
                font-family: PpeikoMedium;
                font-size: 40rem;
                font-weight: 400;
                margin-left: 20vw;
                margin-bottom: 20vh;
            }
            .sello1{
                position: absolute;
                min-width: 10vw;
                min-height: 1.5rem;                
                background-color: #E0BE8D;
                margin-bottom: -95vh;
                margin-left: 145vw;
                z-index: 20;
            }
            .sello2{
                position: absolute;
                min-width: 8vw;
                min-height: 1.5rem; 
                margin-left: 35vw;
                margin-bottom: 30vh;
                background-color: #E0BE8D;
            }
        }
    }
    .sectionTwoBeautyPageThree{
        z-index: 2;
        min-width: 55vw;
        min-height: 100vh;
        background-image: url('../../img/beauty/pagethree/fondo.png');
        background-size: cover;
        background-repeat: no-repeat;
    }
    .sectionThreeBeatyPageThree{
        position: absolute;
        z-index: 3;
        min-width: 100vw;
        min-height: 40vh;
    }
}