.containerHomePageFive{
    margin-top: 10vh;
    margin-bottom: 10vh;
    min-width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: row;

    .sectionOneHomePageFive{
        min-height: 100vh;
        min-width: 65vw;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        hr{
            margin-bottom: 5vh;
            min-width: 50%;
            max-width: 50%;
            margin-left: 0;
            border:1px solid  #707070;
        }
        .text{
            position: absolute;
            margin-top: 10vh;
            margin-right: 40vw;
            min-width: 20vw;
            max-width: 20vw;
            min-height: 10vh;
            color: #E0BE8D;
            font-family: Druk;
            letter-spacing: 1rem;
            font-size: 2rem;
            text-transform: uppercase;
        }
        .sectionFondoOneHomePageFive{
            min-width: 55vw;
            min-height: 100vh;
            background-image: url('../../img/home/pagefive/fondo.png');
            background-repeat: no-repeat;
            background-size: cover;
            background-position: right;
        }
    }
    .sectionTwoHomePageFive{
        margin-left: -8vw;
        min-height: 100vh;
        min-width: 35vw;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        h2{
            min-width: 90%;
            text-align: right;
            margin: 0;
            color: #E0BE8D;
            font-size: 7rem;
            line-height: 1rem;
            font-weight: 400;
            font-family: PpeikoLight;
        }
        h1{
            color: #000;
            min-width: 90%;
            text-align: right;
            line-height: 1rem;
            font-size: 14rem;
            font-family: PpeikoMedium;
            font-weight: 400;
        }
        text{
            margin-left: 8vw;
            min-width: 15vw;
            max-width: 15vw;
            font-size: 1.8rem;            
            display: flex;
            flex-direction: column;
            div{
                float: left;
            }
            text{
                display: flex;
                max-width: 80%;
                min-width: 10vw;
                max-width: 10vw;
                float: left;
                flex-wrap: nowrap;
                z-index: 6;
                font-family: PpeikoLight;
                font-weight: 400;
            }
            img{
                max-width: 3rem;
                max-height: 3rem;
                float: left;
            }
            .comillasDer{
                margin: 0;
                padding: 0;
                max-width: 1.5rem;
                max-height: 1.5rem;
            }
            label{
                min-width: 10vw;
                color: #E0BE8D;
                margin-left: 7vw;
                text-align: right;
            }
            .sello{
                z-index: 3;
                position: absolute;
                background-color: #E0BE8D;
                min-height: 1.5vh;
                min-width: 22vw;
                margin-top: 8vh;                
                margin-left: -5vw;
            }
        }
    }

}