.containerLifeStylePageOne{
    min-width: 100vw;
    min-height: 100vh;
    max-width: 100vw;
    max-height: 100vh;
    display: flex;
    align-items: flex-end;

    .sectionOneLifeStylePageOne{
        z-index: 3;
        min-width: 35vw;
        min-height: 100vh;
        .sectionContentOneLifeStylePageOne{
            min-height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .one{
                position: absolute;
                min-width: 10vw;
                border: 1px solid  #707070;
                margin-bottom: 40vh;
                margin-left: 35vw;
            }
            .two{
                position: absolute;
                min-width: 5vw;
                border: 1px solid  #707070;
                margin-bottom: -20vh;
                margin-left: -30vw;
            }
            h2{
                z-index: 5;
                position: absolute;
                font-family: PpeikoLight;
                font-size: 8rem;
                font-weight: 400;
                color: #E0BE8D;
                margin-top: 2vh;
                margin-left: 15vw;
            }
            h1{
                z-index: 4;
                position: absolute;
                font-family: PpeikoMedium;
                font-size: 25rem;
                font-weight: 400;
                margin-left: 35vw;
            }
            .sello1{
                position: absolute;
                min-width: 5vw;
                min-height: 1.5rem;
                background-color: #E0BE8D;
                margin-top: 15vh;
                margin-left: 7vw;
                z-index: 20;
            }
            .sello2{

            }
        }
    }
    .sectionTwoLifeStylePageOne{
        z-index: 2;
        min-width: 65vw;
        min-height: 100vh;
        background-image: url('../../img/lifestyle/pageone/fondo.png');
        background-size: cover;
        background-repeat: no-repeat;
    }
    .sectionThreeLifeStylePageOne{
        position: absolute;
        z-index: 3;
        min-width: 100vw;
        min-height: 40vh;
    }
}