.containerHomePageTwo{
    min-height: 100vh;
    min-width: 100vw;
    display: flex;
    flex-direction: row;


    .sectionOneHomePageTwo{ 
        z-index: 2;
        min-width: 5vw;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .sello{
            min-height: 70vh;
            display: flex;
            align-items: flex-end;
            div{
                background-color: #E0BE8D;
                min-width: 8vw;
                max-width: 8vw;
                min-height: 18px;
                max-height: 18px;
                margin-right: -4vw;
            }
        }
    }
    .sectionTwoHomePageTwo{
        min-width: 45vw;
        display: flex;
        align-items: flex-end;
        .sectionTwoHomePageTwoFondo{
            min-width: 45vw;
            min-height: 90vh;
            max-height: 90vh;
            background-image: url('../../img/home/pagetwo/fondo.png');
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
        }

    }
    .sectionThreeHomePageTwo{
        min-width: 10vw;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        font-size: 14rem;
        writing-mode: vertical-lr;
        transform: rotate(180deg);
        line-height: 9rem;
    }
    .sectionFourHomePageTwo{
        min-width: 40vw;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        text{
            color: #E0BE8D;
            min-height: 20vh;
            max-height: 20vh;
            min-width:16vw;
            max-width:16vw;
            text-align: left;
            font-size: 2.5rem;
            letter-spacing: 1rem;
            font-family: Druk;
        }
    }
}