.containerAboutPageFour{
    min-width: 100vw;
    min-height: 100vh;
    max-width: 100vw;
    max-height: 100vh;
    display: flex;
    align-items: flex-end;

    .sectionOneAboutPageFour{
        z-index: 3;
        min-width: 35vw;
        min-height: 100vh;
        .sectionContentOneAboutPageFour{
            min-height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .one{
                position: absolute;
                min-width: 10vw;
                border: 1px solid  #707070;
                margin-bottom: 40vh;
                margin-left: 35vw;
            }
            .two{
                position: absolute;
                min-width: 5vw;
                border: 1px solid  #707070;
                margin-bottom: -20vh;
                margin-left: -30vw;
            }
            h2{
                z-index: 5;
                position: absolute;
                font-family: PpeikoLight;
                font-size: 8rem;
                font-weight: 400;
                color: #E0BE8D;
                //margin-top: -5vh;
                margin-left: 15vw;
                margin-bottom: -25vh;
                margin-top: 0;
                writing-mode: vertical-lr;
                transform: rotate(180deg);
            }
            h1{
                z-index: 4;
                position: absolute;
                font-family: PpeikoMedium;
                font-size: 40rem;
                font-weight: 400;
                margin-left: 20vw;
                margin-bottom: 20vh;
            }
            .sello1{
                position: absolute;
                min-width: 40vw;
                min-height: 1.5rem;                
                background-color: #E0BE8D;
                margin-bottom: -95vh;
                //margin-top: 100vh;
                margin-left: 35vw;
                z-index: 20;
            }
            .sello2{
                position: absolute;
                min-width: 5vw;
                min-height: 1.5rem; 
                margin-left: 155vw;
                margin-bottom: 30vh;
                background-color: #E0BE8D;
            }
        }
    }
    .sectionTwoAboutPageFour{
        z-index: 2;
        min-width: 60vw;
        min-height: 100vh;
        background-image: url('../../img/about/pageone/fondo.png');
        background-size: cover;
        background-repeat: no-repeat;
    }
    .sectionThreeAboutPageFour{
        position: absolute;
        z-index: 3;
        min-width: 100vw;
        min-height: 40vh;
    }
}